.Button {
    display: flex;
    align-items: center;
    text-align: center;
    padding: 20px 25px;
    border-radius: 12px;
    /*border: 1px solid #ccc;*/
    /*color: #000;*/
    /*margin-right: 15px;*/
    /*text-transform: uppercase;*/
    font-weight: bold;
    font-size: 12px;
    cursor: pointer;
}
.Button:focus {
    outline: none;
}
.Button:active {
    box-shadow: inset 2px 2px 1px rgba(0,0,0, .3);
}
.Button:disabled {
    background: #ccc;
    color: #000;
    cursor: not-allowed;
}
.error {
background: rgba(240,87,108, 1);
}
.success {
    background: rgba(161,240,69, 1);
}
.primary {
    background: #FF8B37;
color: #fff;
}